import React from 'react';
import { BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom';
import Store from './Store';
import About from './About';
import Contact from './Contact';

function Home() {
  return (
    <div className="center-div">
      <Router>
        <div>
          <ul>
            <li>
              <Link to="/store.html">Store&ensp;</Link>
            </li>
            <li>
              <Link to="/about.html">About&ensp;</Link>
            </li>
            <li>
              <Link to="/contact.html">Contact&ensp;</Link>
            </li>
          </ul>

          {/*
          A <Switch> looks through all its children <Route>
          elements and renders the first one whose path
          matches the current URL. Use a <Switch> any time
          you have multiple routes, but you want only one
          of them to render at a time
        */}
          <Switch>
            <Route path="https://vinjsphp.github.io/store.html">
              <Store />
            </Route>
            <Route path="https://vinjsphp.github.io/about.html">
              <About />
            </Route>
            <Route path="https://vinjsphp.github.io/contact.html">
              <Contact />
            </Route>
          </Switch>
        </div>
      </Router>
    </div>
  );
}

export default Home;
